import React from 'react';
import styled from 'styled-components'
import ReactDOM from 'react-dom';

import { Link, HashRouter, NavLink } from 'react-router-dom';
import { BrowserRouter as Router } from 'react-router-dom'

const Border = styled.div`
    position: absolute;
    width: 0px;
    height: 0px;
    border: 3px solid transparent;
    transition: width 0.2s, top 0.2s;
`

class MenuLink extends Link {    

    repositionBorder = (top, height, width) => {
        document.querySelector('#border').style.top = `${top - 2}px`;
        document.querySelector('#border').style.height = `${height}px`;
        document.querySelector('#border').style.width = `${width}px`;
        document.querySelector('#border').style.border = `3px solid mediumspringgreen`;
    }

    handleBorderReposition = ev => {
        const rect = ev.target.getBoundingClientRect();
        this.repositionBorder(rect.top,rect.height,rect.width)
    }

    render(){
        let injectedProps = {};
        injectedProps.onMouseOver = this.handleBorderReposition;

        return(
            <Link {...this.props} {...injectedProps} />
        )
    }
    
}

const StyledMenuLink = styled(MenuLink)`
  margin-bottom: 10px;
  padding: 10px;
  transition: border 0.5s;
  z-index: 2;
  color: #fff;
  text-decoration: none;
`

const Menu = () => (
    <>
        <StyledMenuLink to="story-mode">Story mode</StyledMenuLink>
        <StyledMenuLink to="chapter-select">Chapter Select</StyledMenuLink>
        <StyledMenuLink to="credits">Credits</StyledMenuLink>
        <Border id="border"/>
    </>
) 

export default Menu